/* LISTS */
ul.inline {
    padding: 0;
}

li {
    display : list-item;
}

ul.inline li {
    display: inline-block;
    padding: 0 0.75em;
}

ul.dropdown-menu > li {
    font-family: inherit;
}

.list-unstyled {
    list-style: none;
    padding-left: 0;
}

ul.list-unstyled > li,
ul.dropdown-menu > li {
    list-style-image : none;
}
