/* BUTTONS */
.btn {
    border-radius: 5px;
    border: none;
    object-fit: cover;
    box-shadow: var(--button-box-shadow);
    padding: 0.7rem 1.2rem;
    margin: 1em auto;
    text-decoration: none;
    color: var(--button-color);
    font-family: var(--font-family-title);
    cursor: pointer;
    touch-action: manipulation;
}

.btn-small {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
}

.btn-large {
    width: 100%;
}

.btn-primary,
.btn-small {
    background-color: var(--button-background-primary);
}

.btn-primary:hover,
.btn-small:hover {
    background-color: var(--button-background-primary-dark);
}

.btn-secondary {
    background-color: var(--button-background-secondary);
    color: var(--button-secondary-color);
}

.btn-secondary:hover {
    background-color: var(--button-background-secondary-dark);
}


.btn-grey {
    background-color: lightgrey;
}

/* OTHER */
div .btn {
    margin: 1em 0;
}

div .btn-padding {
    margin-top: 3em;
}

button:disabled,
.btn:disabled,
button[disabled],
.btn[disabled] {
    opacity: 0.65;
    cursor: not-allowed;
    pointer-events: none;
    background-color: #e2e6ea;
    border-color: #dae0e5;
    color: #6c757d;
    box-shadow: none;
}

button:disabled:hover,
.btn:disabled:hover,
button[disabled]:hover,
.btn[disabled]:hover {
    transform: none;
    transition: none;
}

p,
li,
button,
button.btn,
a,
a.btn {
    white-space: normal;
    word-wrap: break-word;
}

a.btn,
a.btn:visited,
a.btn:hover,
a.btn:visited:hover {
    color: var(--button-link-color);
    text-decoration: none;
}

p:has(.btn) {
    margin: 1em auto;
}

button.btn {
    margin: 0 auto;
}